<template>

<div id="app">



  <div>
    <el-button type="primary" plain>Switch Env V1.0</el-button>
  </div>
<el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="软件配置" name="first">
    <!-- auto       全局居中 width后让div收缩 -->
    <div style="background-color:#fff; box-shadow: 0 0px 40px whitesmoke;box-shadow: 0px 4px 10px darkgrey;padding: 5px;text-align: center;">
      <div style="height:1rem;"></div>
      
      <div style="height:1rem;"></div>
    <el-form ref="form" :model="form">
      <el-form-item label="切换版本" >
        <el-select v-model="form.SoftName" placeholder="请选择软件版本">
          <el-option v-for="(item, index) in applist" :key="index" :label="item.key" :value="item.SoftName+'-'+item.SoftVersion"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="change">切换</el-button>
       
      </el-form-item>
    </el-form>  

    </div>
    </el-tab-pane>
    <el-tab-pane label="版本管理" name="second">
      <el-button type="primary" size="small" style="float:left" @click="dialogFormVisible = true">新增安装包</el-button>
			<el-dialog title="新增软件" :visible.sync="dialogFormVisible" :close-on-click-modal="false">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">

          <el-tooltip class="item" effect="dark" content="请务必保持小写：php" placement="top-start">
            <el-form-item label="软件名称">
            <el-input v-model="formInline.SoftName" placeholder="例如：php"></el-input>
          </el-form-item>
    </el-tooltip>

          <el-form-item label="版本型号">
            <el-input v-model="formInline.SoftVersion" placeholder="例如：74"></el-input>
          </el-form-item>

          <el-form-item label="所在路径">
            <el-tooltip class="item" effect="dark" content="必须以/或\结尾" placement="top-start">
            <el-input v-model="formInline.SoftPath" placeholder="例：D:/Daixs/php73/"></el-input>
          </el-tooltip>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="DoAdd">保存</el-button>
          </el-form-item>
        </el-form>
				        
		</el-dialog>

      <el-table
    :data="tableData"
    style="width: 100%">

    <el-table-column
      label="软件名称">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.SoftName }}-{{ scope.row.SoftVersion }}</span>
      </template>
    </el-table-column>

    <el-table-column
      label="软件路径">
      <template slot-scope="scope">
          <div slot="reference" class="name-wrapper">
            <el-tooltip class="item" effect="dark" :content="scope.row.SoftPath" placement="top-start">
            <el-tag size="medium" @click="handleCopyLink(scope.row)">{{ scope.row.SoftPath }}</el-tag>
          </el-tooltip>
          </div>
        </template>
    </el-table-column>

    
    <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          type="danger"
          @click.native.prevent="deleteRow(scope.$index, tableData)">移除</el-button>
      </template>
    </el-table-column>
  </el-table>
    </el-tab-pane>
    <el-tab-pane label="程序设置" name="third">
      <el-button type="danger" @click="StopApp">终止程序</el-button>
    
    </el-tab-pane>
  </el-tabs>

  
</div>


</template>

<script>
export default {
    data() {
      return {
        dialogFormVisible: false,
        tableData: null,
        applist:null,
        form: {
          SoftName: '',
          SoftVersion:''
        },
        formInline: {
          SoftName: '',
          SoftPath: '',
          SoftVersion:''
        },
        activeName: 'first'
      }
    },
    beforeCreate() {
            document.querySelector('body').setAttribute('style', 'background-color:#525252;')
        },
    created() {

      this.getlist();
            },

    methods: {
      deleteRow(index, rows) {
        // console.log(rows[0].SoftName)
        // console.log(rows[0].SoftVersion)
                    let url = "http://127.0.0.1:8899/delete";
                    //表单格式
                    var params = new URLSearchParams();
                    params.append('name', rows[index].SoftName);
                    params.append('version', rows[index].SoftVersion);
                    //json格式
                    // let params = {
                    //   name:rows[0].SoftName,
                    //   version:rows[0].SoftVersion,
                    // }
                    this.$axios.post(url,params).then(res => {
                      rows.splice(index, 1);
                      this.$notify({
                      title: '移除成功',
                      type: 'success'
                    });

                    })
                    .catch(error =>
                      //请求失败，触发catch中的函数 可省略
                      console.log(error)
                    )

      },
      open1() {
        this.$notify({
          title: '已终止',
          message: '您已成功终止程序运行',
          type: 'success'
        });
      },
      handleCopyLink(row) {
        // console.log(row)
        let _this = this;
        this.$copyText(row.SoftPath).then(//这个地方写自己想要复制出的数据
          function (e) {
            _this.$message({
              showClose: true,
              message: "复制成功",
              type: "success",
            });
          },
          function (e) {
            _this.$message({
              showClose: true,
              message: "复制失败，请手动复制",
              type: "error",
            });
          }
        );
      },
      change() {
        //空输入停止
        if(!this.form.SoftName){
          return;
        }
        
        let url = "http://127.0.0.1:8899/switch";
                    //表单格式
                    var params = new URLSearchParams();
                    const strArr = this.form.SoftName.split('-', 2);
                    params.append('name',strArr[0] );
                    params.append('version', strArr[1]); 
                    this.$axios.post(url,params).then(res => {
                      this.$notify({
                        title: '切换成功',
                        message: '已切换'+strArr[0]+'-'+strArr[1],
                        type: 'success'
                      });
                    })
                    .catch(error =>
                      //请求失败，触发catch中的函数 可省略
                      console.log(error)
                    )



      },
      getlist(){
        let url = "http://127.0.0.1:8899/get_lists";
                    
                    this.$axios.post(url).then(res => {
                      //请求成功，触发then中的函数
                      // let _this = this
                      // this.tableData = res.data;
                      // this.count = res.data.length;
                      this.tableData = res.data.data.list;
                      this.applist = res.data.data.list;
                      
                      // console.log(this.tableData)
      
                    })
                    .catch(error =>
                      //请求失败，触发catch中的函数 可省略
                      console.log(error)
                    )
      },
      DoAdd() {

        if(!this.formInline.SoftName || !this.formInline.SoftVersion || !this.formInline.SoftPath){
          return;
        }

        let endPath = this.formInline.SoftPath.substring(this.formInline.SoftPath.length - 1);
        // console.log(endPath)
        if(endPath == '\\' || endPath == '/'){
          let url = "http://127.0.0.1:8899/create";
                    //表单格式
                    var params = new URLSearchParams();
                    params.append('name', this.formInline.SoftName);
                    params.append('version',this.formInline.SoftVersion);
                    params.append('path', this.formInline.SoftPath);
                    //json格式
                    // let params = {
                    //   name:rows[0].SoftName,
                    //   version:rows[0].SoftVersion,
                    // }
                    this.$axios.post(url,params).then(res => {

                      if(res.data.code == 500){
                        this.$notify({
                            title: '软件版本已存在',
                            type: 'error'
                          });
                          return;
                      }

                           this.$notify({
                            title: '新增成功',
                            message: '已新增'+this.formInline.SoftName+'-'+this.formInline.SoftVersion+"路径："+this.formInline.SoftPath,
                            type: 'success'
                          });
                          
                          this.getlist();


                          //清空表单
                          this.formInline = [];

                          //隐藏
                          this.dialogFormVisible = false;

                    })
                    .catch(error =>
                      //请求失败，触发catch中的函数 可省略
                      console.log(error)
                    )

        }else{
          this.$notify({
                            title: '路径必须以 /或\\结尾',
                            type: 'error'
                          });
                          return;
        }
        


      },
      StopApp(){
        this.open1();
      },
      handleClick(tab, event) {
        console.log(tab, event);
      }
    }
  }


</script>

<style>

/* margin: auto; width:350px;padding: 5%;background-color: rgb(243, 246, 253);text-align: center; */

#app {

margin: auto;
 width: 380px;
 padding: 5%;
 min-height: 300px;
 background-color: #ffffff;;
 border-radius: 10px;
 margin-top: 55px;
 text-align: center;
}
</style>



